import axios from '../axios'

//
export const uploadPic = (data) => {
    return axios({
        url: 'uploadPic',
        method: 'post',
        data
    })
}

//
export const getPicBaseUrl = (data) => {
    return axios({
        url: 'getPicBaseUrl',
        method: 'post',
        data
    })
}

//
export const province = (data) => {
    return axios({
        url: '/province',
        method: 'post',
        data
    })
}

// 
export const queryCodeTable = (data) => {
    return axios({
        url: '/codeTable',
        method: 'post',
        data
    })
}
