import axios from 'axios';
import config from './config';
// import Cookies from "js-cookie";
import router from '@/router'

// vuexloading
// import store from '@/store'
export default function $axios(options) {
  return new Promise((resolve, reject) => {
    const instance = axios.create({
      baseURL: config.baseUrl,
      headers: config.headers,
      timeout: config.timeout,
      withCredentials: config.withCredentials
    })
    // request 
    instance.interceptors.request.use(
      config => {
        const token = sessionStorage.getItem('paike_token');
        config.headers.token = token
        // config.data.examId=examId
        return config
      },

      error => {
        // 
        console.log('request:', error)
        // 1. 
        if (error.message.indexOf('timeout') !== -1) {
          console.log('timeout')
        }
        // 2. 
        const errorInfo = error.response
        console.log(errorInfo)
        if (errorInfo) {
          error = errorInfo.data // catch,Promise.reject
          // const errorStatus = errorInfo.status; // 404 403 500 ...
          router.push({
            name: "404"
          })
        }
        return Promise.reject(error) // (catch)
      }
    )
    // response 
    instance.interceptors.response.use(
      response => {
        let data;
        // IE9response.dataundefinedresponse.request.responseText(Stringify)
        if (response.data === undefined) {
          data = JSON.parse(response.request.responseText)
        } else {
          if (options.responseType === 'blob') {
            data = {
              blob: response.data,
              headers: response.headers
            }
          } else {
            data = response.data
          }
          // data = response.data
        }

        // code
        switch (data.rc) {
          case 1:
            console.log(data.desc)
            break;
          case 0:
            // store.commit('changeState')
            // console.log('')
            break;
          default:
        }
        // code
        // const err = new Error(data.desc)
        // err.data = data
        // err.response = response
        // throw err
        // console.log(data);
        return data
      },
      err => {
        if (err && err.response) {
          switch (err.response.status) {
            case 400:
              err.message = ''
              break
            case 401:
              err.message = ''
              break
            case 403:
              err.message = ''
              break
            case 404:
              err.message = `: ${err.response.config.url}`
              break
            case 408:
              err.message = ''
              break
            case 500:
              err.message = ''
              break
            case 501:
              err.message = ''
              break
            case 502:
              err.message = ''
              break
            case 503:
              err.message = ''
              break
            case 504:
              err.message = ''
              break
            case 505:
              err.message = 'HTTP'
              break
            default:
          }
        }
        console.error(err)
        return Promise.reject(err) // 
      }
    )

    // 
    // instance(options).then(res => {
    instance({
      ...options,
      data: {
        ...config.data,
        // token: sessionStorage.getItem('fenban_token'),
        // data: { ...options.data }
        data: options.data
      }
    }).then(res => {
      resolve(res)
      return false
    }).catch(error => {
      reject(error)
    })
  })
}