import axios from "../../../../axios";

/**
 * @description 
 * @param { Object } params
 * @returns { Promise }
 */

// 
export const getClassByGrade = (data) => {
  return axios({
    url: "/CA_CourseTable/getClassByGrade",
    method: "post",
    data,
  });
};

